body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Fade in up animation for cards and elements */
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Pulse text animation */
@keyframes pulseText {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

/* Spin animation for rotating elements */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/*********************************************************
  Login Page Styles
*********************************************************/
/* Container geral da p√°gina */
.page-container {
  min-height: 100vh;
  background: url('images/background.png') no-repeat center center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Organiza√ß√£o de linhas e colunas */
.page-row,
.page-col {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-col {
  max-width: 400px;
  width: 100%;
}

/* Card do formul√°rio */
.card-container {
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 30px;
  padding: 20px;
  color: #fff;
  position: relative;
  overflow: hidden;
  border: 1px solid #fff;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  opacity: 0;
  animation: fadeInUp 0.5s ease-out forwards;
}

.card-container:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(255, 255, 255, 0.2);
}

/* Cabe√ßalho */
.page-header {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 10px 0 20px;
  color: #fff;
}

/* Corpo do card */
.card-body {
  padding: 20px;
}

/* Logo */
.logo-container {
  text-align: center;
  margin-bottom: 20px;
}

.logo-image {
  max-width: 150px;
}

/* Formul√°rio */
.form-container {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

.input-email,
.input-password {
  background-color: #2b2b2b;
  border: 1px solid #444;
  color: #fff;
  border-radius: 4px;
  padding: 10px;
}

.input-email::placeholder,
.input-password::placeholder {
  color: #999;
}

/* Bot√£o */
.submit-btn {
  width: 100%;
  background-color: #444;
  color: #fff;
  border: none;
  padding: 12px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #666;
}

/* Rodap√© */
.footer-text {
  margin-top: 10px;
  text-align: center;
  color: #fff;
}

.footer-link {
  color: #fff;
  text-decoration: underline;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: #999;
}

/* SweetAlert Custom */
.custom-swal {
  background-color: #1c1c1c;
  color: #fff;
  border-radius: 16px;
}

.custom-swal-title {
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}

.custom-swal-text {
  color: #ccc;
  font-size: 16px;
}

/*********************************************************
  Navigation Styles
*********************************************************/
.nav-container {
