}

/* Override Bootstrap dropdown menu styles for mobile */
@media (max-width: 991px) {
  .dropdown-menu {
    width: 100% !important;
    background-color: #1c1c1c;
    border: none;
  }
}

/* Hide mobile menu on larger screens */
@media (min-width: 992px) {
  .mobile-menu-overlay {
