/* .card-bg √© um plano de fundo desfocado dentro do card,
   usado para dar um efeito de profundidade na imagem de fundo.
*/

.card-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  filter: blur(8px);
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.3);
  margin: 5px;
}


.img-bg{
  position: absolute;
  background-size: cover;
  background-position: center;
  filter: blur(4px);
}

/* .inner-card-body mant√©m o conte√∫do em cima do plano de fundo desfocado */
